<template>
	<section>

		<div class="calc_title_area">
			<h2 class="text-primary">Takmer hotovo!</h2>
			<!-- view_header_text -->
			<div v-html="html.view_header_text"></div>
		</div>

		<!-- navrh poistnej zmluvy a vsetky o poistnikovi ci poisteni -->
		<!-- contract_html -->
		<div v-html="html.contract_html" class="prop_wording"></div>

		<!-- dokumenty -->
		<Documents_prepare :start_doc_requests="start_doc_requests" @all_docs_finished="all_docs_finished" step_html="4" />

		<!-- kym nebudu nacitane dokumenty zobraz loading -->
		<div v-show="!doc_requests_are_done" class="text-center pt-2 pb-5">
				<h3>Načítavame dokumenty</h3>
				<Bouncer_loading />
		</div>

		<!-- confirm formular a navigacia -->
		<div v-show="doc_requests_are_done" class="prop_confirm">


			<h3 class="text-primary">
				Záverečné potvrdenie
			</h3>

			<!-- vzdy pritomny marketingovy suhlas + akceptovanie elektronickej komunikacie -->
			<div class="confirm_check">
				<div class="form-check">
					<input
						name="suhlas_elekt_kom"
						id="suhlas_elekt_kom"
						v-model="checkboxes.suhlas_elekt_kom"
						true-value="1"
						false-value="0"
						type="checkbox"
						class="form-check-input"
						:class="this.invalid_checkboxes ? 'required' : ''"
					>
					<label
						for="suhlas_elekt_kom"
						class="form-check-label"
					>
						<!-- elekt_kom_modal_trigger -->
						<button
							@click="marketing_ek_modal"
							class="btn-text text-start"
						>
							Súhlas s elektronickou komunikáciou
						</button>
					</label>
				</div>
				<div class="form-check">
					<input
						name="suhlas_marketing"
						id="suhlas_marketing"
						v-model="checkboxes.suhlas_marketing"
						true-value="1"
						false-value="0"
						type="checkbox"
						class="form-check-input"
					>
					<label
						for="suhlas_marketing"
						class="form-check-label"
					>
						<!-- marketing_modal_trigger -->
						<button
							@click="marketing_modal"
							class="btn-text text-start"
						>
							Súhlas so spracovaním osobných údajov pre účely marketingu
						</button>
					</label>
				</div>
			</div>

			<!-- pravny suhlas -->
			<div id="checkbox_row" class="confirm_check">
				<p>
					{{ wording.paragraph }}
				</p>
				<div class="form-check">
					<input
						name="suhlas_navrh"
						id="suhlas_navrh"
						v-model="checkboxes.suhlas_navrh"
						true-value="1"
						false-value="0"
						type="checkbox"
						class="form-check-input"
						:class="this.invalid_checkboxes ? 'required' : ''"
					>
					<label
						for="suhlas_navrh"
						class="form-check-label"
					>
						{{ wording.label }}
					</label>
				</div>
			</div>

			<!-- navigacia a error message -->
				<div class="row form-pagination">
					<div class="col-12">
						<!-- error message -->
						<p v-show="invalid_checkboxes" class="text-accent">
							Pred pokračovaním musíte potvrdiť zvýraznené zaškrtávacie políčka
						</p>
					</div>
					<div class="col-12 col-sm-6 text-center text-sm-start order-2 order-sm-1">
						<!-- back -->
						<button
							@click="edit_user_data"
							class="btn btn-white btn-left btn-lg"
						>
							Upraviť
						</button>
					</div>
					<div class="col-12 col-sm-6 text-center text-sm-end order-1 order-sm-2">
						<!-- confirm button -->
						<button
							@click="run_confirm"
							v-show="!confirm_running"
							type="button"
							class="btn btn-accent btn-right btn-lg"
						>
							<span>{{ wording.span }}</span>
						</button>
						<!-- confirm loading -->
						<Bouncer_loading v-show="confirm_running" />
					</div>
				</div>
		</div>

	</section>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import Session_data from '@/logic/requests/Session_data.js';
	import Calculate_requests from '@/logic/requests/Calculate_requests.js';
	import Get_form_html from '@/logic/requests/Get_form_html.js';
	import Confirm_requests from '@/logic/requests/Confirm_requests.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_params from '@/store/STATE_params.js';
	import Documents_prepare from '@/components/form_app/documents/Documents_prepare.vue';

	export default {
		props: {
		},
		components: {
			Documents_prepare,
			Bouncer_loading,
		},
		data() {
			return {
				debug: false,
				start_doc_requests: false,
				doc_requests_are_done: false,
				confirm_running: false,
				mode: '', // classic_confirm | provider_agent | provider_client | sms_agent | sms_client
				product_id: 0,
				invalid_checkboxes: false,

				special_data: {
					confirmed_by_provider: false,
					require_provider_confirm: false,
				},
				signature_required: 0,

				html: {
					contract_html: '',
					view_header_text: '',
				},

				checkboxes: {
					suhlas_elekt_kom: '0',
					suhlas_marketing: '0',
					suhlas_navrh: '0',
				},
			}
		},
		created() {
			Promise.resolve().then(
				() => {
					return Session_data.session_info();
				}
			).then(
				(response) => {
					this.special_data = response.result?.specialData;
					this.signature_required = response.result?.signature_required;
				}
			).then(
				() => {
					return Session_data.get_data();
				}
			).then(
				(response) => {
					STATE_params().extract_selected_product(response);
					this.product_id = STATE_params().get_selected_product().product_id;
				}
			).then(
				() => {
					return Calculate_requests.check_calculate();
				}
			).then(
				(response) => {
					// check for validate error in calculates request
					// since they merged calculate call with validate call, calculate can receive validate error as well
					// f.e. zaciatok poistenia nemoze byt v minulosti
					if (response.result.validform === false) {
						console.log('calculate validform false');
						STATE_modals().store_modal_data(response.result.form_validation_error_elements);
						STATE_modals().open_it('calculate_error_in_validate');
						return false;
					}
					// check if there wasnt any inapropriate data entered in step3
					else if (response.result[this.product_id]?.result?.errors !== undefined) {
						console.log('calculate form error');
						STATE_modals().store_modal_data(response.result[this.product_id].result.errors);
						STATE_modals().open_it('calculate_error_in_form');
						return false;
					}
					else if (response.status !== 200) {
						// some common error states like 422 already confirmed
						console.log('error in calculate:', response.result.message);
						return false;
					}
					// find out which mode we run
					this.confirm_mode_definition();
					// debug skips templates and docs
					if (this.debug) {
						this.doc_requests_are_done = true;
						this.checkboxes.suhlas_elekt_kom = '1';
						this.checkboxes.suhlas_navrh = '1';
						return false;
					}
					// if we received no errors, run rest of requests
					return true;
				}
			).then(
				(go) => {
					if (!go) {
						return;
					}
					// html templates
					Get_form_html.get_it(false).then(
						(response) => {
							this.html.contract_html = response.result?.html;
						}
					);
					Get_form_html.get_it('view_header_text').then(
						(response) => {
							this.html.view_header_text = response.result?.html;
						}
					);
					// start document requests, when it finishes it sends emit "all_docs_finished" to show confirm form
					this.start_doc_requests = true;
				}
			);
		},
		methods: {
			edit_user_data()
			{
				STATE_core().change_step(3);
			},
			all_docs_finished()
			{
				this.doc_requests_are_done = true;
			},
			confirm_mode_definition()
			{
				// restore_session is entered in email URL and contains token
				// therefor its missing for a agent but present for client
				let restore_session = null; // TEMP HARD CODED !!!
				// let restore_session = get_url_parameter('restore_session');
				// tiez tu mame URL parameter &auto_confirm=on

				// few products require signature on provider website
				if (this.special_data.require_provider_confirm === true) {
					if (restore_session === null && this.special_data.confirmed_by_provider === false) {
						// this.send_agent_proposal();
						this.mode = 'provider_agent';
					} else if (this.special_data.confirmed_by_provider === false) {
						// this.get_sms_code();
						this.mode = 'provider_client';
					} else if (this.special_data.confirmed_by_provider === true /*&& get_url_parameter('auto_confirm') === 'on'*/) {
						console.log('AGENT type 2nd visit');
						// confirm_session();
					}
				}
				// for most product groups signature is not required
				else if (this.signature_required === 0) {
					// confirm_session();
					this.mode = 'classic_confirm';
				}
				// sms confirm AGENT and CLIENT has different "confirm" button HTML, modals and APIs
				else if (restore_session === null ) {
					// this.send_agent_proposal();
					this.mode = 'sms_agent';
				} else {
					// this.get_sms_code();
					this.mode = 'sms_client';
				}
			},
			run_confirm()
			{
				if (this.mode !== 'classic_confirm') {
					console.log('ERROR: only classic_confirm works for now, other types of confirm (provider, sms) not yet implemented!');
					return;
				}
				// chechboxes validation
				if (this.checkboxes.suhlas_elekt_kom === '0' || this.checkboxes.suhlas_navrh === '0') {
					this.invalid_checkboxes = true;
					return;
				}
				this.invalid_checkboxes = false;
				this.confirm_running = true;
				Confirm_requests.confirm_session(this.checkboxes).then(
					(response) => {
						this.confirm_running = false;
						// dunno how often this happens, just a fallback
						if (response.status !== 200) {
							console.log('confirm error', response);
							return;
						}
						// success
						if (response.result?.response === 'success'/*confirm*/ || response.result?.state === 100/*getsessioninfo*/) {
							STATE_core().change_step(5);
							return;
						}
						// baaad data
						console.log('confirm simple error');
						STATE_modals().store_modal_data(response.result);
						STATE_modals().open_it('confirm_simple_error');
					}
				);
			},
			marketing_modal()
			{
				STATE_modals().open_it('marketing');
			},
			marketing_ek_modal()
			{
				STATE_modals().open_it('marketing_ek');
			},
		},
		computed: {
			wording()
			{
				let wording = {
					classic_confirm: {
						span: 'Dokončiť',
						paragraph: '',
						label: 'Uvedený návrh na uzatvorenie poistnej zmluvy som si prečítal(a) a súhlasím s ním. Týmto prehlasujem, že som bol oboznámený(á) s vyššie uvedenou predzmluvnou dokumentáciou, obsahu som porozumel(a) a týmto vyjadrujem súhlas.',
					},
					sms_agent: {
						span: 'Odoslať návrh emailom',
						paragraph: 'Pre dokončenie zmluvy o poskytnutí finančnej služby je potrebné potvrdenie klienta formou SMS autorizácie.',
						label: 'Totožnosť klienta som overil a súhlasím s odoslaním dokumentácie na email klienta, aby dokončil autorizáciu.',
					},
					sms_client: {
						span: 'Vyžiadať SMS kód',
						paragraph: 'Pre dokončenie zmluvy o poskytnutí finančnej služby je potrebné potvrdenie cez SMS kód - kliknutím na tlačitko "vyžiadať sms kód".',
						label: 'Oboznámil som sa s predzmluvnou dokumentáciou, nemám žiadne výhrady s jej obsahom a žiadam o zaslanie SMS kódu na moje telefónne číslo.',
					},
					provider_agent: {
						span: 'Odoslať dokumentáciu emailom',
						paragraph: 'Pre akceptáciu predzmluvnej dokumentácie je potrebné potvrdenie klienta formou SMS autorizácie a pre dokončenie zmluvy je potrebné doplniť chýbajúce údaje na webe providera a zmluvu podpísať.',
						label: 'Totožnosť klienta som overil a súhlasím s odoslaním dokumentácie na email klienta, aby dokončil autorizáciu. Oboznámil som sa s predzmluvnou dokumentáciou, nemám žiadne výhrady s jej obsahom a žiadam o presmerovanie na stránku providera.',
					},
					provider_client: {
						span: 'Vyžiadať SMS kód',
						paragraph: 'Pre akceptáciu predzmluvnej dokumentácie je potrebné potvrdenie klienta formou SMS autorizácie a pre dokončenie zmluvy je potrebné doplniť chýbajúce údaje na webe providera a zmluvu podpísať.',
						label: 'Oboznámil som sa s predzmluvnou dokumentáciou, nemám žiadne výhrady s jej obsahom a žiadam o zaslanie SMS kódu na moje telefónne číslo.',
					},
				};
				if (!wording.hasOwnProperty(this.mode)) {
					return '';
				}
				return wording[this.mode];
			},
		},
	}
</script>
