<template>
	<div class="row mt-4 form-pagination" data-cy="navigation">
		<div class="col-12 col-sm-4 text-center text-sm-start order-2 order-sm-1">
			<button
				v-if="show_back"
				@click="prev"
				type="button"
				class="btn btn-light btn-left btn-lg"
			>
				Naspäť
			</button>
		</div>
		<div class="col-12 col-sm-4 text-center order-3 order-sm-2">
			<button
				@click="clear"
				type="button"
				class="btn btn-white btn-lg"
			>
				Vymazať údaje
			</button>
		</div>
		<div class="col-12 col-sm-4 text-center text-sm-end order-1 order-sm-3">
			<button
				@click="next"
				:disabled="disable_next"
				:title="disable_next ? 'Pokúšame sa zopakovať požiadavku.' : ''"
				type="button"
				class="btn btn-primary btn-right btn-lg"
			>
				Pokračovať
			</button>
		</div>
	</div>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import STATE_params from '@/store/STATE_params.js';
	import STATE_error from '@/store/STATE_error.js';
	import STATE_forms from '@/store/STATE_forms.js';

	export default {
		props: {
			show_back: Boolean,
			disable_next: Boolean,
		},
		components: {
		},
		emits: ['next', 'prev'],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			next() {
				this.$emit('next');
			},
			prev() {
				this.$emit('prev');
			},
			clear() {
				STATE_params().$reset();
				STATE_error().$reset();
				STATE_forms().$reset();
				STATE_core().set_session_token('', true);
				STATE_core().change_step(-1);
				setTimeout(
					() => { STATE_core().change_step(1) },
					100
				);
			},
		},
		computed: {
		},
	}
</script>
